	import {
		mapMutations, mapState
	} from 'vuex';
	import {logout} from "@/api/login";
	export default {
		data() {
			return {
				loadProgress: 0,
				CustomBar: this.CustomBar,
				currentStorageSize: 0,
				user: {},
				userInfo:{}
			};
		},
		onLoad () {
			this.initData();
		},
		computed: {
			...mapState(['userInfo']),
		},
		methods:{
			...mapMutations(['login', 'logout']),
			initData () {
        this.userInfo = uni.getStorageSync('userInfo');
				const _this = this;
				uni.getStorageInfo({
					success (res) {
						
						_this.currentStorageSize = res.currentSize;
						console.log(_this.currentStorageSize)
					}
				})
			},
			navTo(url){
				if (url === '清除缓存') {
					uni.showModal({
				    content: '确定要清除缓存吗',
				    success: (e)=>{
				    	if(e.confirm){
				    		this.currentStorageSize = 0;
				    		uni.clearStorageSync();
                this.$api.msg('清除缓存成功');
                setTimeout(() => {
                  this.login(this.userInfo);
                  uni.reLaunch({
                      url: '/pages/user/user'
                  });
                }, 1.5 * 1000);
				    		this.LoadProgress();
				    	}
				    }
					});
				} else if (!url) {
					this.$api.msg('我还没写');
				} else {
					uni.navigateTo({
						url,
					})
				}
			},
			LoadProgress() {
				this.loadProgress = this.loadProgress + 6;
				if (this.loadProgress < 100) {
					setTimeout(() => {
						this.LoadProgress();
					}, 100)
				} else {
					this.loadProgress = 0;
          this.login(this.user);
          this.$api.msg('清除缓存成功')
				}
			},
			//退出登录
			toLogout(){
				uni.showModal({
				    content: '确定要退出登录么',
				    success: (e)=>{
				    	if(e.confirm){
				    	//	this.$post(`${logout}`).then(r => {
									this.logout();
									uni.reLaunch({
										url: '/pages/user/user'
									})
								//})
				    	}
				    }
				});
			},
			//switch
			switchChange(e){
				let statusTip = e.detail.value ? '打开': '关闭';
				this.$api.msg(`${statusTip}消息推送`);
			},

		}
	}
